<template>
	<div class="main-content ofh" v-bind:class="[isMobile, isNotMobile]">
		<g-header></g-header>
		<div class="wrapper">
			<div class="pageContent">
				<el-row>
					<el-col :span="24">
						<slot></slot>
					</el-col>
				</el-row>
			</div>
		</div>
		<g-footer></g-footer>
	</div>
</template>

<script>
	import GHeader from './Header'
	import GFooter from './Footer'

	export default {
		name: 'Wrapper',
		components: {
			GHeader,
			GFooter
		},
		computed: {
			isMobile() {
				return {
					'is-mobile': window.innerWidth < 415,
				}
			},
			isNotMobile() {
				return {
					'is-not-mobile': window.innerWidth >= 415
				}
			}
		}
	}
</script>

<style lang="scss">
@import "../assets/scss/components/_vars";

@media screen {
	.is-mobile {
		margin-left: 0px;
	}	
}

@media screen {
	.is-not-mobile {
		margin-left: calc(100% -#{$sideBarWidth});
	}
}
</style>
